import { Component } from '@angular/core';
import { NavController, LoadingController, ToastController, Datetime } from '@ionic/angular';
import { FileTransfer, FileUploadOptions, FileTransferObject } from '@ionic-native/file-transfer/ngx';
import { Camera, CameraOptions } from '@ionic-native/camera/ngx';
import { File } from '@ionic-native/file/ngx';
import { Http, Headers, RequestOptions } from '@angular/http';
import { CommunityService } from '../../app/services/community.service';
import { PhotoViewer } from '@ionic-native/photo-viewer/ngx';
import { AlertController } from '@ionic/angular';
import { ImagePicker } from '@ionic-native/image-picker/ngx';
import { GalleryPage } from '../gallery/gallery.page';
import { MasterDetailService } from '../../app/services/masterdetail.service';


@Component({
  selector: 'app-home',
  templateUrl: 'home.page.html',
  styleUrls: ['home.page.scss']
})
export class HomePage {
    imgSource: any;
    imageURI: any;
    imageFileName: any;
    loader: any;
    public photos: any;
    public base64Image: string;
    imageDetail;
    objImageList: any;

    constructor(public navCtrl: NavController,
        private transfer: FileTransfer,
        private camera: Camera,
        private file: File,
        public loadingCtrl: LoadingController,
        public toastCtrl: ToastController,
        public httpClient: Http,
        private communityService: CommunityService,
        private photoViewer: PhotoViewer,
        public alertController: AlertController,
        private imagePicker: ImagePicker,
        private masterDetailService: MasterDetailService) { }

    public images: Array<string>;
    private grid: Array<Array<string>>;

    ngOnInit() {
        this.presentAlertMultipleButtons();
        //this.getImageList();
        
    }

    async presentAlertMultipleButtons() {
        const alert = await this.alertController.create({
            header: 'Attention!',
            message: 'Please select the environment',
            buttons: [
                {
                    text: 'Android',
                    role: 'cancel',
                    cssClass: 'secondary',
                    handler: (blah) => {
                        this.communityService.baseUrl = "http://10.0.2.2:49168/api";
                        this.getImageList();
                        console.log('Confirm Cancel: blah');
                    }
                }, {
                    text: 'Browser',
                    handler: () => {
                        // this.navCtrl.navigateForward('add_news');
                        this.communityService.baseUrl = "http://localhost:49168/api";
                        this.getImageList();
                        console.log('Confirm Okay');

                    }
                }
            ]
        });

        await alert.present();
    }

    LoadImage(imgUrl){
        this.photoViewer.show(imgUrl);
        
    }

    LoadImagePicker() {
        let options = {
            maximumImagesCount: 5,
            width: 500,
            height: 500,
            quality: 75,
            outputType: 1
        }
        
        this.imagePicker.getPictures(options).then(
            (file_uris) => {
                console.log(file_uris);
                this.masterDetailService.setUris(file_uris);
                this.navCtrl.navigateForward('gallery');
            },(err) => { console.log('uh oh'); }
        );
    }

    getImageList() {
        this.communityService.getImageList().subscribe(resp => {
            this.objImageList = resp;
            console.log(this.objImageList);
            this.grid = Array(Math.ceil(this.objImageList.length / 2));
            this.populateGrid();
        });
        //this.objcommunity = this.communityService.getCommunity(id);
        //console.log(this.objcommunity);

    }

    populateGrid() {
        let rowNum = 0;

        for (let i = 0; i < this.objImageList.length; i += 2) {

            this.grid[rowNum] = Array(2);

            if (this.objImageList[i]) {
                this.grid[rowNum][0] = this.objImageList[i].imgUrl
            }

            if (this.objImageList[i + 1]) {
                this.grid[rowNum][1] = this.objImageList[i+1].imgUrl
            }

            rowNum++;
        }
    }

    getImage() {
        const options: CameraOptions = {
            quality: 100,
            destinationType: this.camera.DestinationType.DATA_URL,
            encodingType: this.camera.EncodingType.JPEG,
            mediaType: this.camera.MediaType.PICTURE,
            sourceType: this.camera.PictureSourceType.PHOTOLIBRARY

        }

        this.camera.getPicture(options).then((imageData) => {

            //this.base64Image = imageData.toDataURL();


            ////this.base64.encodeFile(imageData).then((base64File: string) => {
            ////    this.base64Image = base64File;
            ////}, (err) => {
            ////    console.log(err);
            ////    });

            //this.base64Image = "data:image/jpeg;base64," + imageData
            //var base64Image = new Buffer(imageData, 'binary').toString('base64');


            this.base64Image = imageData


            //this.file.resolveLocalFilesystemUrl(imageData).then(oneFile => {
            //    this.imageFileName = oneFile.name;
            //    this.file.readAsArrayBuffer(this.file.tempDirectory, oneFile.name).then(realFile=>{
            //        this.imageURI = realFile;
            //        console.log(this.imageFileName);
            //    });
            //});
            
        }, (err) => {
            console.log(err);
            this.presentToast(err);
        });
    }

    async presentLoading() {
        const loading = await this.loadingCtrl.create({
            message: 'Uploading...',
            duration: 3000
        });
        return await loading.present();
    }

    getDateTime() {
        var currentdate = new Date();
        var datetime =  currentdate.getDay() + "-" + currentdate.getMonth()
            + "" + currentdate.getFullYear() + "-"
            + currentdate.getHours() + "-"
            + currentdate.getMinutes() + "-" + currentdate.getSeconds();
        return datetime;
    }
    uploadFile() {
        this.presentLoading();

        var headers = new Headers();
        headers.append("Accept", 'application/json');
        headers.append('Content-Type', 'application/json');
        const requestOptions = new RequestOptions({ headers: headers });
        const fileTransfer: FileTransferObject = this.transfer.create();

        //let options: FileUploadOptions = {
        //    fileKey: 'abcd',
        //    fileName: 'xyz.jpg',
        //    chunkedMode: false,
        //    mimeType: "image/jpeg",
        //    httpMethod: 'post',
        //    headers: {}
        //}
        //this.imageDetail = "{ imgName: xyz.jpg, imgData:" + this.base64Image + "}";

        let picDetail = {
            "imgName": "APP-"+ this.getDateTime()  + ".jpg",
            "imgData": this.base64Image
        }

        //this.httpClient.post('http://10.0.2.2:49168/api/ImageUpload/File/xyz.jpg/', picDetail)
        this.httpClient.post('http://10.0.2.2:49168/api/ImageUpload/File/xyz.jpg/', picDetail, requestOptions)
            .subscribe(data => {
                console.log(data['_body']);
                this.imgSource = data['_body']
                this.loadingCtrl.dismiss();
                this.presentToast("Image uploaded successfully");
                this.getImageList();
                //this.navCtrl.navigateForward('tabs/(news:news)');
             }, error => {
                 console.log(error);
                 this.loadingCtrl.dismiss();
                 this.presentToast(error);
            });

        ////this.imageDetail= "{ imgName: xyz.jpg, imgData:" + this.base64Image + "}";
        ////fileTransfer.upload(this.imageDetail, 'http://10.0.2.2:49168/api/ImageUpload/File/xyz.jpg/', options)
        ////    .then((data) => {
        ////        console.log(data + " Uploaded Successfully");
        ////        this.imageFileName = "https://s3-us-west-2.amazonaws.com/azcommunityimages/IMG_20181107_201027.jpg"
        ////        //loader.dismiss();
        ////        this.loadingCtrl.dismiss();
        ////        this.presentToast("Image uploaded successfully");
        ////    }, (err) => {
        ////        console.log(err);
        ////        this.loadingCtrl.dismiss();
        ////        this.presentToast(err);
        ////    });
    }

    async presentToast(msg) {
        const toast = await this.toastCtrl.create({
            message: msg,
            duration: 3000,
            position: 'bottom'
        });

        //toast.onDidDismiss(() => {
        //    console.log('Dismissed toast');
        //});

        toast.present();
    }
}


